export function compareVerify(a, b) {
    if (!a) {
        return 1;
    }
    else {
        return -1;
    }
}
export function compareYear(a, b) {
    if (a.year > b.year) {
        return 1;
    }
    else {
        return -1;
    }
}
export function compareDate(a, b) {
    if (a > b) {
        return 1;
    }
    else {
        return -1;
    }
}
export function shortPem(pem) {
    const begin = "-----BEGIN CERTIFICATE-----";
    const end = "-----END CERTIFICATE-----";
    const cleanPem = pem.replace(begin, "").replace(end, "");
    return [cleanPem.substring(0, 100), cleanPem.substring(pem.length - 200)];
}
