import { PEMObject } from "pem-ts";
export function valid(pemString) {
    try {
        const pem = new PEMObject();
        pem.decode(pemString);
        return true;
    }
    catch (e) {
        return false;
    }
}
export function verify(pemString) {
    const pem = new PEMObject();
    pem.decode(pemString);
    const result = wasmVerifyMraCert(buf2hex(pem.data));
    return result;
}
function buf2hex(buffer) {
    return [...new Uint8Array(buffer)]
        .map((x) => x.toString(16).padStart(2, "0"))
        .join("");
}
